import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'SellerMemberInfo',
    setup() {
        const shopName = ref('');
        const headimgurl = ref('');
        const shopLevel = ref(0);
        const levelName = ref('');
        const upgradeScore = ref(0);
        const levelBarWidth = ref(0);
        const shopLevelList = ref([]);
        const getSellerInfo = () => {
            http
                .post('/hbSeller/sellerPerson/personInfo', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    shopName.value = res.data.personInfo.shopName;
                    headimgurl.value = res.data.personInfo.headimgurl;
                    shopLevel.value = res.data.personInfo.shopLevel;
                    levelName.value = res.data.levelCode.levelName;
                    upgradeScore.value = res.data.upgradeScore;
                    const levelScore = res.data.levelCode.levelScoreMax -
                        res.data.levelCode.levelScoreMin +
                        1;
                    levelBarWidth.value =
                        ((levelScore - upgradeScore.value) / levelScore) * 100;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        const getMemberInfo = () => {
            http
                .post('/hbSeller/sellerPerson/shopLevelList', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    shopLevelList.value = res.data.levelCodeList;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        onMounted(() => {
            getSellerInfo();
            getMemberInfo();
        });
        return {
            shopName,
            headimgurl,
            shopLevel,
            levelName,
            upgradeScore,
            levelBarWidth,
            shopLevelList
        };
    }
});
